"use client";

import { useRef } from "react";
import { Canvas, useFrame } from "@react-three/fiber";
import { useGLTF, Float, Environment, ContactShadows, PresentationControls } from "@react-three/drei";
import * as THREE from "three";

function Macbook(props: any) {
  // Using a reliable public model from React Three Fiber examples market
  const { scene } = useGLTF("https://vazxmixjsiawhamofees.supabase.co/storage/v1/object/public/models/macbook/model.gltf");
  return <primitive object={scene} {...props} />;
}

// Simple Monitor placeholder if we can't find a perfect matching style, 
// but let's try to use a similar style model if available, or just a generic screen.
// For now, I'll use just the Macbook as it's the most standard "tech" asset, 
// and maybe add a simple geometric shape or another model if requested.
// The user asked for "macbook and dekhtop". "Dekhtop" likely means Desktop/Monitor.
// Let's try to find a monitor model or just use the Macbook for now and see if the user likes it,
// or I can build a simple monitor with geometry.

function Monitor(props: any) {
    const { scene } = useGLTF("https://vazxmixjsiawhamofees.supabase.co/storage/v1/object/public/models/monitor-round/model.gltf");
    return <primitive object={scene} {...props} />;
}

export default function FloatingTech() {
  return (
    <div style={{ width: "100%", height: "100%" }}>
      <Canvas camera={{ position: [0, 0, 15], fov: 45 }}>
        <ambientLight intensity={0.5} />
        <spotLight position={[10, 10, 10]} angle={0.15} penumbra={1} intensity={1} castShadow />
        <pointLight position={[-10, -10, -10]} intensity={1} />
        
        <PresentationControls
            global
            config={{ mass: 2, tension: 500 }}
            snap={{ mass: 4, tension: 1500 }}
            rotation={[0, 0.3, 0]}
            polar={[-Math.PI / 6, Math.PI / 6]}
            azimuth={[-Math.PI / 6, Math.PI / 6]}
        >
            <Float rotationIntensity={0.4} floatIntensity={2} floatingRange={[0.2, 1]}>
                {/* Macbook */}
                <Macbook position={[-3, -1, 0]} rotation={[0.2, 0.5, 0]} scale={2.5} />
                
                {/* Monitor / Desktop */}
                <Monitor position={[3, -0.5, -1]} rotation={[0.1, -0.4, 0]} scale={2.5} />
            </Float>
        </PresentationControls>
        
        <ContactShadows position={[0, -4.5, 0]} opacity={0.4} scale={20} blur={2} far={4.5} />
        <Environment preset="city" />
      </Canvas>
    </div>
  );
}
